.background-container {
   background-color: #FD6CBB;
    width: 100%;
    height: 500px;
}

.calendar-container {
    width: 55%;
    margin: auto;
    position: relative;
    top: -500px;
    padding-top: 200px;
}
.weekdays-container {
    display: flex;
    margin: auto;
    justify-content: space-between;
    background: white;
}

.weekdays-container, .week-day{
    color: #808080;
  
}

.calendar {
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.calendar .week {
    display: inline-flex;
    align-items: flex-start;
    justify-content: space-around;
    width: 100%;
    height: 100%;
    background-color: white;
}
.week-day{
    width: 100%;
    padding-left: 65px;
}

.calendar .week .date  {
    width: 30px;
  
    height: 100%;
    padding: 30px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}

button {
    background: white;
    border: none;
}

.datepicker-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: white;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.datepicker-container span {
    font-size: 20px;
}

.ChevronIcon{
    border-radius: 100px;
    background: #e8e8e8;
    padding: 10px 10px;
    margin: 10px;

}

.today {
    background-color: #FD6CBB;
    color: white;
    border-radius: 5px;
    filter: drop-shadow(2px 2px 2px #FD6CBB)
}
.selectedDateNumber {
    border-radius: 50px;
    background-color: #FD6CBB;
}
.handleChange {
    background-color: #FD6CBB;
}
.date.selected {
    background-color: #FD6CBB;
}

.month-scroll-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: sans-serif;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .month-scroll-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 5px 10px;
  }
  
  .month-scroll-header h2 {
    font-size: 24px;
    margin: 0;
  }
  
  .month-scroll-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  .month-scroll-menu svg {
    width: 24px;
    height: 24px;
  }
  
  .month-scroll-dropdown {
    list-style: none;
    margin: 0;
    padding: 5px;
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    border-radius: 5px;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 1;
  }
  
  .month-scroll-dropdown li {
    padding: 5px;
    cursor: pointer;
  }
  
  .month-scroll-dropdown li:hover {
    background-color: #ddd;
  }
  